@font-face {
    font-family: grafierDisp;
    src: url('./grafier-display.otf');
  }
  @font-face {
    font-family: grafierText;
    src: url('./grafier-text.otf');
  }
  

@media (min-width: 479px) {

  nav{
    height: 90px;
    width: 100%;
    display: flex;
  }
  
  body{
    margin: 0;
  }
  
  h1{
    font-family: sans-serif;
    font-size: 50px;
    font-weight: normal;
    margin-top: -20px;
  }
  
  h2{
    font-family: sans-serif;
    font-size: 24px;
    font-weight: normal;
    margin-top: 0;
    margin-bottom: 10px;
  
  }
  
  h3{
    font-family: sans-serif;
    font-size: 15px;
    font-weight: normal;
    margin-top:0;
  
  }
  
  .nav{
    display: inline-block;
    margin-top: auto;
    margin-bottom: 25px;
    margin-left: 60px;
    margin-right: 30px;
    padding-top: 20px;
  }
  
  #search{
    opacity: 1;
    width: 60%;
    
  }
  
  .subheader{
    height: 60px;
    display: flex;
  }
  
  #categories{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    /* background-color: grey; */
  }
  
  #locName{
    height: 100%;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center !important;
  }
  p{
    font-family: grafierDisp, serif;
    font-size: 30px;
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .head{
    display: inline-block;
    margin-left: 20px;
  }
  
  #crank{
    width: 10%;
    margin-left:40px;
  }
  #cloc{
    width: 30%;
  }
  #cscore{
    width:30%;
  }

  #cicon{
    width: 22.5%;
    padding-left: 7.5%;
  }
  
  
  
  
  input{
    margin-top: auto;
    width: 100%;
    height: 50px;
    border: 0px none;
    border-bottom: 2px solid grey;
    border-radius: 0px;
    font-family: sans-serif;
    font-size: 24px;
    outline: 0;
    -webkit-appearance: none;
  }
  #navlist{
  
    border-bottom: 2px solid black;
    border-radius: 0px;
  
  }
  

  
  #main{
    width: 100%;
    display: flex;
  }
  
  #list{
    width:60%;
    height: 100%;
    background-color: #A7CCAF;
    overflow: hidden;
    display: inline-block;
    margin: 0;
  }

  #listmobile{
    width:100%;
    height: 100%;
    background-color: #A7CCAF;
    overflow: hidden;
    display: inline-block;
    margin: 0;
  }
  
  #listelement{
    height: 80px;
    border-bottom: 1px solid #65856C;
    font-family: grafierDisp, serif;
    font-size: 25px;
    display: flex;
    align-items: center;
  }
  .elementelement{
    display: inline-block;
    margin-left: 20px;
  }
  #rank{
    width: 10%;
    margin-left:40px;
  }
  #location{
    width: 30%;
  }
  #score{
    width:30%;
  }
  #icon{
    width:10%;
    height: 100%;
  }

  #icon img{
    max-height: 70%;
    margin-top: 10px;
  }
  .sidebar{
    width: 300px;
    height: 100%;
    margin: 0;
  }
  
  .localinfo{
    width: 100%;
    height:80%;
    background-color: #F16343;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center !important;
    overflow: hidden;
  }
  
  .logo{
    height:20%;
    background-color: #A3A2A2;
    display: flex;
    justify-content: center;
  }
  
  .logo img{
    max-height: 80%;
    margin-top: auto;
    margin-bottom: auto;
  }
  
  .localinfo img{
    max-width: 60%;
    margin-bottom: 20px;
    max-height: 20%;
  }
  #localscore{
    margin-top: 20px;
  }
  
  ::-webkit-scrollbar {display:none;}

  .searchItem {
    height: auto;
    cursor: pointer;
    opacity: 1;
    background-color: #FFFFFF;	
  } .searchItem:hover {
    background-color: #E5E5E5;
    opacity: 1;
  }

  .searchListElement{
    opacity: 1;
  }

  .localpercentage {
    padding: 0; 
    margin: 0;
  }

  .localpercentagebottom {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
  }

}

